*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

:root {
  --color-primary-accent: #007aff;
  --color-primary-accent-hover: #005ecb;
  --color-text-primary: #1d1d1f;
  --color-text-secondary: #6e6e73;
  --color-text-placeholder: #c7c7cc;
  --color-text-on-accent: #ffffff;

  --color-background-app: #f5f5f7;
  --color-background-content: #ffffff;
  --color-background-sidebar: #f0f2f5;
  --color-background-hover: #e9e9ed;
  --color-background-hover-alpha: #005ecb10;
  --color-background-active: #ddeaff;
  --color-background-input: #ffffff;

  --color-border-light: #e0e0e0;
  --color-border-medium: #cccccc;
  --color-divider: var(--color-border-light);

  --color-danger: #ff3b30;
  --color-danger-hover: #d92c23;
  --color-success: #34c759;
  --color-warning: #ff9500;

  --font-sans: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  --font-mono: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, Courier,
    monospace;

  --spacing-xs: 0.25rem;
  --spacing-sm: 0.5rem;
  --spacing-md: 1rem;
  --spacing-lg: 1.5rem;
  --spacing-xl: 2rem;

  --border-radius-small: 4px;
  --border-radius-medium: 8px;
  --border-radius-large: 12px;

  --sidebar-width: 240px;
}

html {
  font-size: 16px;
  line-height: 1.6;
  font-family: var(--font-sans);
  background: var(--color-background-app);
  color: var(--color-text-primary);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  min-height: 100vh;
  background: var(--color-background-app);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: var(--font-sans);
  margin-bottom: var(--spacing-sm);
  font-weight: 600;
  line-height: 1.3;
}
h1 {
  font-size: 1.75rem;
}
h2 {
  font-size: 1.25rem;
}

a {
  color: var(--color-primary-accent);
  text-decoration: none;
}
a:hover {
  text-decoration: underline;
  color: var(--color-primary-accent-hover);
}

button {
  font-family: var(--font-sans);
  font-size: 0.9rem;
  cursor: pointer;
  background: var(--color-primary-accent);
  color: var(--color-text-on-accent);
  border: none;
  padding: var(--spacing-sm) var(--spacing-md);
  border-radius: var(--border-radius-medium);
  font-weight: 500;
  transition: background-color 0.2s ease;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: var(--spacing-xs);
  line-height: 1.4;
}
button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

input,
textarea,
select {
  font-family: var(--font-sans);
  font-size: 0.9rem;
  padding: var(--spacing-sm);
  border: 1px solid var(--color-border-light);
  border-radius: var(--border-radius-small);
  background: var(--color-background-input);
  color: var(--color-text-primary);
  width: 100%;
  line-height: 1.4;
}
input:focus,
textarea:focus,
select:focus {
  outline: none;
  border-color: var(--color-primary-accent);
  box-shadow: 0 0 0 2px var(--color-primary-accent);
}
textarea {
  resize: vertical;
  min-height: 60px;
}

label {
  font-size: 0.85rem;
  color: var(--color-text-secondary);
  display: inline-flex;
  gap: var(--spacing-xs);
}

/*──────────────────────────────── Layout ───────────────────────────────*/
.layout {
  display: flex;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

/*──────────────────────────────── Sidebar ───────────────────────────────*/
.sidebar {
  width: var(--sidebar-width);
  padding: var(--spacing-md);
  background: var(--color-background-sidebar);
  border-right: 1px solid var(--color-divider);
  display: flex;
  flex-direction: column;
  overflow-y: hidden;
}
.sidebar h2 {
  margin-bottom: var(--spacing-md);
  padding-left: var(--spacing-sm);
}
.sidebar ul#project-list {
  flex: 1;
  overflow-y: auto;
  list-style: none;
  padding: 0;
  margin: 0;
}
.sidebar li.project-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: var(--spacing-sm);
  gap: var(--spacing-xs);
  font-size: 0.9rem;
  border-radius: var(--border-radius-small);
  color: var(--color-text-primary);
  margin-bottom: var(--spacing-xs);
  cursor: pointer;
}
.sidebar li.project-item:hover {
  background: var(--color-background-hover);
}
.sidebar li.project-item.active {
  background: var(--color-background-active);
  color: var(--color-primary-accent);
  font-weight: 600;
}
.action-icon-btn-sidebar {
  all: unset;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  width: 24px;
  height: 24px;
  padding: var(--spacing-xs);
  border-radius: 50%;
  color: var(--color-text-secondary);
  transition: color 0.15s ease-in-out;
}
button.delete-project-btn,
.action-icon-btn-sidebar {
  background: transparent;
}
button.delete-project-btn .material-icons-outlined,
.action-icon-btn-sidebar .material-icons-outlined {
  font-size: 1.1rem;
}
button.delete-project-btn:hover .material-icons-outlined,
.action-icon-btn-sidebar:hover .material-icons-outlined {
  color: var(--color-danger);
}
button.delete-project-btn:focus,
.action-icon-btn-sidebar:focus {
  outline: none;
}

/*──────────────────────────────── Main Content ───────────────────────────────*/
.main-content {
  flex: 1;
  padding: var(--spacing-lg);
  background: var(--color-background-content);
  overflow-y: auto;
}
.main-content h1.text-center {
  margin-bottom: var(--spacing-lg);
}

/*──────────────────────────────── Toolbar ───────────────────────────────*/
.toolbar {
  display: flex;
  gap: var(--spacing-sm);
  margin-bottom: var(--spacing-md);
}
.toolbar button {
  padding: var(--spacing-xs) var(--spacing-sm);
  font-size: 0.85rem;
  background: var(--color-background-sidebar);
  color: var(--color-text-primary);
  border: 1px solid var(--color-border-light);
}
.toolbar button:hover {
  background: var(--color-background-hover);
}
.toolbar button.active-sort {
  background: var(--color-primary-accent);
  color: var(--color-text-on-accent);
  border-color: var(--color-primary-accent);
}
.toolbar button.active-sort:hover {
  background: var(--color-primary-accent-hover);
  border-color: var(--color-primary-accent-hover);
}

/*──────────────────────────────── Forms ───────────────────────────────*/
.proj-form {
  display: flex;
  gap: var(--spacing-sm);
  margin-top: var(--spacing-md);
}
.proj-form input#proj-input {
  flex: 1;
  height: 2.25rem;
}
.proj-form button[type='submit'] {
  flex-shrink: 0;
  height: 2.25rem;
}

.todo-form {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-sm);
  background: var(--color-background-sidebar);
  padding: var(--spacing-md);
  border-radius: var(--border-radius-medium);
  margin-bottom: var(--spacing-lg);
  align-items: center;
}
.todo-form > select#todo-priority {
  flex: 1;
  min-width: 120px;
}
.todo-form > input#todo-input {
  flex: 2;
  min-width: 200px;
  height: 2.25rem;
}
.todo-form > .todo-due-label,
.todo-form > .todo-priority-label {
  display: inline-flex;
  align-items: center;
  flex: 1;
  min-width: auto;
  gap: var(--spacing-xs);
  height: 2.25rem;
}
.todo-form > .todo-due-label input {
  width: auto;
}
.todo-form > select#todo-project {
  flex: 1;
  min-width: 150px;
}
.todo-form > button[type='submit'] {
  flex-shrink: 0;
  height: 2.25rem;
}

/*──────────────────────────────── Task List ───────────────────────────────*/
.todo-list {
  list-style: none;
  padding: 0;
  margin: 0;
}
.todo-list li {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  gap: var(--spacing-lg);
  padding: var(--spacing-sm) var(--spacing-md);
  margin-bottom: var(--spacing-sm);
  background: var(--color-background-content);
  border: 1px solid var(--color-border-light);
  border-radius: var(--border-radius-medium);
  transition: background-color 0.2s ease;
}
.todo-list li:hover {
  background: var(--color-background-hover-alpha);
}
.todo-list li.done {
  opacity: 0.7;
}
.todo-list li.done .todo-text {
  text-decoration: line-through;
  color: var(--color-text-secondary);
}

/*──────────────────────────────── Priority & Text ───────────────────────────────*/
.todo-list .todo-priority {
  flex-shrink: 0;
  display: inline-flex;
  align-items: center;
  color: var(--color-text-secondary);
}
.todo-list .todo-priority.high .material-icons-outlined {
  color: var(--color-danger);
}
.todo-list .todo-priority.medium .material-icons-outlined {
  color: var(--color-warning);
}
.todo-list .todo-priority.low .material-icons-outlined {
  color: var(--color-primary-accent);
}

.todo-list .todo-text {
  flex: 1;
  min-width: 0;
  font-size: 0.95rem;
  margin-left: 1rem;
  line-height: 1.4;
  color: var(--color-text-primary);
  word-break: break-word;
  align-self: center;
}

.todo-list li {
  display: flex;
  align-items: center;
}

.todo-list .todo-due {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-xs);
}

/*──────────────────────────────── Action-Icon Buttons ───────────────────────────────*/
.todo-list .action-icon-btn,
.action-icon-btn-sidebar {
  all: unset;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding: var(--spacing-xs);
  border-radius: 50%;
  color: var(--color-text-secondary);
  transition: background-color 0.15s ease, color 0.15s ease;
  flex-shrink: 0;
}
.todo-list .action-icon-btn:hover,
.todo-list .action-icon-btn:focus {
  background: var(--color-background-hover);
  outline: none;
}
.todo-list .action-icon-btn.list-btn.all-done .material-icons-outlined {
  color: var(--color-success);
}

.todo-list .action-icon-btn:hover .material-icons-outlined {
  color: var(--color-primary-accent-hover);
}

.todo-list .action-icon-btn.remove:hover .material-icons-outlined {
  color: var(--color-danger-hover);
}

.edit-task-form {
  display: none;
}

.todo-list .subtask-text {
  text-align: left;
}

.todo-list .action-icon-btn.list-btn.all-done .material-icons-outlined {
  color: var(--color-success);
}
.todo-list .action-icon-btn.note-btn.has-notes .material-icons-outlined {
  color: var(--color-primary-accent);
}
.todo-list .action-icon-btn.note-btn.has-notes:hover .material-icons-outlined {
  color: var(--color-primary-accent-hover);
}

.todo-list .subtask-count.all-done {
  color: var(--color-success);
}

/*──────────────────────────────── Edit-Task Form ───────────────────────────────*/
.edit-task-form {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-sm);
  align-items: center;
  width: 100%;
}
.edit-task-form .edit-input {
  padding: var(--spacing-xs);
  font-size: 0.9rem;
  border-radius: var(--border-radius-small);
}
.edit-task-form .edit-task-text {
  flex: 2;
  min-width: 150px;
}
.edit-task-form .edit-task-due {
  flex: 1;
  min-width: 130px;
}
.edit-task-form .edit-task-priority {
  flex: 1;
  min-width: 110px;
}
.edit-task-form .edit-task-category {
  flex: 1;
  min-width: 110px;
}

.edit-form-actions {
  display: flex;
  justify-content: flex-end;
  gap: var(--spacing-sm);
  width: 100%;
  margin-top: var(--spacing-sm);
}
button.save-edit-btn {
  background: var(--color-primary-accent);
}
button.cancel-edit-btn {
  background: var(--color-background-hover);
  color: var(--color-text-primary);
  border: 1px solid var(--color-border-light);
}
button.cancel-edit-btn:hover {
  background: var(--color-border-light);
}

/*──────────────────────────────── Notes & Subtasks ───────────────────────────────*/
.note-area,
.list-area {
  width: 100%;
  margin-top: var(--spacing-sm);
  border-top: 1px solid var(--color-divider);
  padding-top: var(--spacing-sm);
  padding-left: calc(30px + var(--spacing-sm) * 2);
}
.note-area textarea {
  width: 100%;
}
.note-area-actions,
.sub-form-actions {
  display: flex;
  justify-content: flex-end;
  gap: var(--spacing-sm);
  margin-top: var(--spacing-sm);
}
button.cancel-note-btn,
button.cancel-subtask-form-btn {
  background: var(--color-background-hover);
  color: var(--color-text-secondary);
  border: 1px solid var(--color-border-light);
}
button.cancel-note-btn:hover,
button.cancel-subtask-form-btn:hover {
  background: var(--color-border-light);
}

/*──────────────────────────────── Confirmation Dialog ───────────────────────────────*/
#confirmation-dialog {
  border: 1px solid var(--color-border-medium);
  border-radius: var(--border-radius-medium);
  padding: var(--spacing-lg);
  min-width: 320px;
  max-width: 400px;
  background: var(--color-background-content);
  color: var(--color-text-primary);
  margin: auto;
}
#confirmation-dialog::backdrop {
  background: rgba(0, 0, 0, 0.3);
}
.confirmation-form {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-xl);
}
#confirmation-message {
  font-size: 1.1rem;
  line-height: 1.5;
}
.dialog-actions {
  display: flex;
  justify-content: flex-end;
  gap: var(--spacing-sm);
}
#confirm-no-btn {
  background: var(--color-background-hover);
  color: var(--color-text-primary);
  border: 1px solid var(--color-border-light);
}
#confirm-no-btn:hover {
  background: var(--color-border-light);
}
#confirm-yes-btn:hover:not(:disabled) {
  background: var(--color-primary-accent-hover);
}

/*──────────────────────────────── Hidden Utility Class ───────────────────────────────*/
.hidden {
  display: none;
}

.todo-list li {
  display: flex;
  align-items: center;
}

.todo-list .todo-text {
  padding: 0;
  display: inline-flex;
  align-items: center;
}

.todo-list .todo-due {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-xs);
}

/*──────────────────────────────── Due Date Hover ───────────────────────────────*/
.todo-list .todo-due:hover {
  color: var(--color-primary-accent-hover);
}
.todo-list .todo-due:hover .material-icons-outlined {
  color: var(--color-primary-accent-hover);
}

/*──────────────────────────────── Sticky App Header ────────────────────────────*/
.app-header {
  position: sticky;
  top: 0;
  background-color: var(--color-primary-accent);
  color: white;
  z-index: 1000;
}

.app-header .container {
  display: flex;
  align-items: center;
  padding: var(--spacing-md) var(--spacing-lg);
}

#add-project-btn:hover,
#add-task-btn:hover {
  background-color: var(--color-primary-accent-hover);
}

/*────────────────── Unified Button Border Radius ──────────────────*/
button,
.todo-list .action-icon-btn,
.action-icon-btn-sidebar {
  border-radius: var(--border-radius-medium);
}

/*───────────────── More spacing in each task li ──────────────────*/
.todo-list li {
  gap: var(--spacing-xl);
}

.todo-list li > .task-group-left,
.todo-list li > .task-group-right {
  gap: var(--spacing-xl);
}

/*───────────────── Action Buttons Uniform Height ──────────────────*/
.proj-form button[type='submit'],
.todo-form button[type='submit'],
.edit-form-actions button,
.note-area-actions button,
.sub-form-actions button {
  height: 2.25rem;
  line-height: 2.25rem;
}

.sub-form-actions button {
  margin-bottom: var(--spacing-sm);
}
