ckground-content: #ffffff;\n  --color-background-sidebar: #f0f2f5;\n  --color-background-hover: #e9e9ed;\n  --color-background-hover-alpha: #005ecb10;\n  --color-background-active: #ddeaff;\n  --color-background-input: #ffffff;\n\n  --color-border-light: #e0e0e0;\n  --color-border-medium: #cccccc;\n  --color-divider: var(--color-border-light);\n\n  --color-danger: #ff3b30;\n  --color-danger-hover: #d92c23;\n  --color-success: #34c759;\n  --color-warning: #ff9500;\n\n  --font-sans: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,\n    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;\n  --font-mono: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, Courier,\n    monospace;\n\n  --spacing-xs: 0.25rem;\n  --spacing-sm: 0.5rem;\n  --spacing-md: 1rem;\n  --spacing-lg: 1.5rem;\n  --spacing-xl: 2rem;\n\n  --border-radius-small: 4px;\n  --border-radius-medium: 8px;\n  --border-radius-large: 12px;\n\n  --sidebar-width: 240px;\n}\n\nhtml {\n  font-size: 16px;\n  line-height: 1.6;\n  font-family: var(--font-sans);\n  background: var(--color-background-app);\n  color: var(--color-text-primary);\n  -webkit-font-smoothing: antialiased;\n  -moz-osx-font-smoothing: grayscale;\n}\n\nbody {\n  min-height: 100vh;\n  background: var(--color-background-app);\n}\n\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n  font-family: var(--font-sans);\n  margin-bottom: var(--spacing-sm);\n  font-weight: 600;\n  line-height: 1.3;\n}\nh1 {\n  font-size: 1.75rem;\n}\nh2 {\n  font-size: 1.25rem;\n}\n\na {\n  color: var(--color-primary-accent);\n  text-decoration: none;\n}\na:hover {\n  text-decoration: underline;\n  color: var(--color-primary-accent-hover);\n}\n\nbutton {\n  font-family: var(--font-sans);\n  font-size: 0.9rem;\n  cursor: pointer;\n  background: var(--color-primary-accent);\n  color: var(--color-text-on-accent);\n  border: none;\n  padding: var(--spacing-sm) var(--spacing-md);\n  border-radius: var(--border-radius-medium);\n  font-weight: 500;\n  transition: background-color 0.2s ease;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  gap: var(--spacing-xs);\n  line-height: 1.4;\n}\nbutton:disabled {\n  opacity: 0.5;\n  cursor: not-allowed;\n}\n\ninput,\ntextarea,\nselect {\n  font-family: var(--font-sans);\n  font-size: 0.9rem;\n  padding: var(--spacing-sm);\n  border: 1px solid var(--color-border-light);\n  border-radius: var(--border-radius-small);\n  background: var(--color-background-input);\n  color: var(--color-text-primary);\n  width: 100%;\n  line-height: 1.4;\n}\ninput:focus,\ntextarea:focus,\nselect:focus {\n  outline: none;\n  border-color: var(--color-primary-accent);\n  box-shadow: 0 0 0 2px var(--color-primary-accent);\n}\ntextarea {\n  resize: vertical;\n  min-height: 60px;\n}\n\nlabel {\n  font-size: 0.85rem;\n  color: var(--color-text-secondary);\n  display: inline-flex;\n  gap: var(--spacing-xs);\n}\n\n/*──────────────────────────────── Layout ───────────────────────────────*/\n.layout {\n  display: flex;\n  height: 100vh;\n  width: 100vw;\n  overflow: hidden;\n}\n\n/*──────────────────────────────── Sidebar ───────────────────────────────*/\n.sidebar {\n  width: var(--sidebar-width);\n  padding: var(--spacing-md);\n  background: var(--color-background-sidebar);\n  border-right: 1px solid var(--color-divider);\n  display: flex;\n  flex-direction: column;\n  overflow-y: hidden;\n}\n.sidebar h2 {\n  margin-bottom: var(--spacing-md);\n  padding-left: var(--spacing-sm);\n}\n.sidebar ul#project-list {\n  flex: 1;\n  overflow-y: auto;\n  list-style: none;\n  padding: 0;\n  margin: 0;\n}\n.sidebar li.project-item {\n  display: flex;\n  align-items: center;\n  justify-content: space-between;\n  padding: var(--spacing-sm);\n  gap: var(--spacing-xs);\n  font-size: 0.9rem;\n  border-radius: var(--border-radius-small);\n  color: var(--color-text-primary);\n  margin-bottom: var(--spacing-xs);\n  cursor: pointer;\n}\n.sidebar li.project-item:hover {\n  background: var(--color-background-hover);\n}\n.sidebar li.project-item.active {\n  background: var(--color-background-active);\n  color: var(--color-primary-accent);\n  font-weight: 600;\n}\n.action-icon-btn-sidebar {\n  all: unset;\n  display: inline-flex;\n  align-items: center;\n  justify-content: center;\n  cursor: pointer;\n  width: 24px;\n  height: 24px;\n  padding: var(--spacing-xs);\n  border-radius: 50%;\n  color: var(--color-text-secondary);\n  transition: color 0.15s ease-in-out;\n}\nbutton.delete-project-btn,\n.action-icon-btn-sidebar {\n  background: transparent;\n}\nbutton.delete-project-btn .material-icons-outlined,\n.action-icon-btn-sidebar .material-icons-outlined {\n  font-size: 1.1rem;\n}\nbutton.delete-project-btn:hover .material-icons-outlined,\n.action-icon-btn-sidebar:hover .material-icons-outlined {\n  color: var(--color-danger);\n}\nbutton.delete-project-btn:focus,\n.action-icon-btn-sidebar:focus {\n  outline: none;\n}\n\n/*──────────────────────────────── Main Content ───────────────────────────────*/\n.main-content {\n  flex: 1;\n  padding: var(--spacing-lg);\n  background: var(--color-background-content);\n  overflow-y: auto;\n}\n.main-content h1.text-center {\n  margin-bottom: var(--spacing-lg);\n}\n\n/*──────────────────────────────── Toolbar ───────────────────────────────*/\n.toolbar {\n  display: flex;\n  gap: var(--spacing-sm);\n  margin-bottom: var(--spacing-md);\n}\n.toolbar button {\n  padding: var(--spacing-xs) var(--spacing-sm);\n  font-size: 0.85rem;\n  background: var(--color-background-sidebar);\n  color: var(--color-text-primary);\n  border: 1px solid var(--color-border-light);\n}\n.toolbar button:hover {\n  background: var(--color-background-hover);\n}\n.toolbar button.active-sort {\n  background: var(--color-primary-accent);\n  color: var(--color-text-on-accent);\n  border-color: var(--color-primary-accent);\n}\n.toolbar button.active-sort:hover {\n  background: var(--color-primary-accent-hover);\n  border-color: var(--color-primary-accent-hover);\n}\n\n/*────────